.StoryIntro p {
  text-align: center;
  font-size: 1.5rem;
}

.StoryIntro img, .pic img {
  margin: auto;
  display: block;
}

:root {
  --primary: #16d5ff;
  --secondary: #999;
  --light: #f2f8fe;
  --dark: #111;
}

.back-to-top {
  z-index: 99;
  display: none;
  position: fixed;
  bottom: 60px;
  right: 30px;
}

h4, h5, h6, .h4, .h5, .h6 {
  font-weight: 500 !important;
}

#spinner {
  opacity: .7;
  visibility: hidden;
  z-index: 99999;
  transition: opacity .5s ease-out, visibility 0s linear .5s;
}

#spinner.show {
  visibility: visible;
  opacity: 1;
  transition: opacity .5s ease-out, visibility linear;
}

.btn {
  font-weight: 500;
  transition: all .5s;
}

.btn.btn-primary {
  color: #fff;
}

.btn-square {
  width: 38px;
  height: 38px;
}

.btn-sm-square {
  width: 32px;
  height: 32px;
}

.btn-lg-square {
  width: 48px;
  height: 48px;
}

.btn-square, .btn-sm-square, .btn-lg-square {
  justify-content: center;
  align-items: center;
  padding: 0;
  font-weight: normal;
  display: flex;
}

.navbar.sticky-top {
  transition: all .5s;
  top: -100px;
}

.navbar .navbar-brand {
  height: 75px;
}

.navbar .navbar-nav .nav-link {
  color: var(--dark);
  outline: none;
  margin-right: 30px;
  padding: 25px 0;
  font-weight: 500;
}

.navbar .navbar-nav .nav-link:hover, .navbar .navbar-nav .nav-link.active {
  color: var(--primary);
}

.navbar .dropdown-toggle:after {
  content: "";
  vertical-align: middle;
  border: none;
  margin-left: 8px;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

@media (max-width: 991.98px) {
  .navbar .navbar-nav .nav-link {
    margin-right: 0;
    padding: 10px 0;
  }

  .navbar .navbar-nav {
    border-top: 1px solid #eee;
  }

  .titleIcon {
    display: none;
  }

  .title {
    background: url("bg2.a3372ae7.jpg");
  }

  .R_words {
    text-align: left;
  }

  .bigWords {
    display: none;
  }
}

@media (min-width: 992px) {
  .navbar .nav-item .dropdown-menu {
    opacity: 0;
    visibility: hidden;
    border: none;
    margin-top: 0;
    transition: all .5s;
    display: block;
    top: 150%;
  }

  .navbar .nav-item:hover .dropdown-menu {
    visibility: visible;
    opacity: 1;
    transition: all .5s;
    top: 100%;
  }

  .title {
    background: url("bg.18f340df.jpg");
  }

  .R_words {
    text-align: right;
  }

  .smallWords {
    display: none;
  }
}

.navbar .btn:hover {
  color: #fff !important;
  background: var(--primary) !important;
}

.hero-header .breadcrumb-item + .breadcrumb-item:before {
  color: var(--secondary);
}

.header {
  margin: 0;
}

.service-item {
  position: relative;
  overflow: hidden;
}

.service-item:after {
  content: "";
  width: 0;
  height: 0;
  opacity: 1;
  border: 80px solid;
  transition: all .5s;
  position: absolute;
  top: -80px;
  right: -80px;
  transform: rotate(45deg);
}

.service-item.bg-white:after {
  border-color: transparent transparent var(--light) transparent;
}

.service-item.bg-light:after {
  border-color: #0000 #0000 #fff;
}

.service-item:hover:after {
  opacity: 0;
}

.roadmap-carousel {
  position: relative;
}

.roadmap-carousel:before {
  content: "";
  height: 0;
  width: 100%;
  border-top: 2px dashed var(--secondary);
  position: absolute;
  top: 20px;
  left: 0;
}

.roadmap-carousel .roadmap-item .roadmap-point {
  width: 30px;
  height: 30px;
  border: 2px solid var(--primary);
  background: #fff;
  justify-content: center;
  align-items: center;
  margin-left: -15px;
  display: flex;
  position: absolute;
  top: 6px;
  left: 50%;
  transform: rotate(45deg);
}

.roadmap-carousel .roadmap-item .roadmap-point span {
  width: 18px;
  height: 18px;
  background: var(--primary);
  display: block;
}

.roadmap-carousel .roadmap-item {
  text-align: center;
  padding-top: 150px;
  position: relative;
}

.roadmap-carousel .roadmap-item:before {
  content: "";
  width: 0;
  height: 115px;
  border-right: 2px dashed var(--secondary);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.roadmap-carousel .roadmap-item:after {
  content: "";
  width: 14px;
  height: 14px;
  background: var(--primary);
  margin-left: -7px;
  position: absolute;
  top: 111px;
  left: 50%;
  transform: rotate(45deg);
}

.roadmap-carousel .owl-nav {
  justify-content: center;
  margin-top: 40px;
  display: flex;
}

.roadmap-carousel .owl-nav .owl-prev, .roadmap-carousel .owl-nav .owl-next {
  width: 50px;
  height: 50px;
  color: var(--light);
  background: var(--primary);
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  margin: 0 12px;
  font-size: 22px;
  transition: all .5s;
  display: flex;
}

.roadmap-carousel .owl-nav .owl-prev:hover, .roadmap-carousel .owl-nav .owl-next:hover {
  color: var(--primary);
  background: var(--dark);
}

.accordion .accordion-item {
  border: none;
  margin-bottom: 15px;
}

.accordion .accordion-button {
  background: var(--light);
  border-radius: 2px;
}

.accordion .accordion-button:not(.collapsed) {
  color: #fff;
  background: var(--primary);
  box-shadow: none;
}

.accordion .accordion-button:not(.collapsed):after {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}

.accordion .accordion-body {
  padding: 15px 0 0;
}

.footer .btn.btn-link {
