}

.footer .btn.btn-link:before {
  content: "";
  color: var(--secondary);
  margin-right: 10px;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: relative;
}

.footer .btn.btn-link:hover {
  color: var(--primary);
  letter-spacing: 1px;
  box-shadow: none;
}

.footer .btn.btn-square {
  color: var(--primary);
  border: 1px solid var(--primary);
}

.footer .btn.btn-square:hover {
  color: #fff;
  background: var(--primary);
}

.footer .copyright {
  border-top: 1px solid #1111111a;
  padding: 25px 0;
}

.footer .copyright a {
  color: var(--primary);
}

.footer .copyright a:hover {
  color: var(--dark);
}

.footer {
  margin: 0;
}

